export default {
  name: 'Pack',

  data() {
    return {
      packs: [],
      currentPack: {}
    };
  },

  methods: {
    packChange(index) {
      // this.$toast('当前 Swipe 索引：' + index);
      this.currentPack = this.packs[index];
      console.log(this.currentPack);
    },

    selectPack() {
      this.$router.push({
        name: 'Confirm',
        params: {
          'pack_id': this.currentPack.index
        }
      });
    },

    onClickLeft() {
      this.$router.go(-1);
    }

  },
  filters: {
    leftNumber(value) {
      return "剩余：" + str(value) + "件";
    }

  },

  created() {
    this.$axios.get('api/packs').then(res => {
      let {
        message,
        data,
        code
      } = res.data;
      console.log(res.data);

      if (code == 200) {
        this.packs = data;
        this.currentPack = data[0];
      }
    });
  }

};